<template>
<!-- 待办列表页面 -->
  <div class="overhaul-manual">
    <div class="title">
      <navBar :titleText="$route.query.waitDealtName"></navBar>
    </div>
    <div v-show="waitDealtList.length != 0" class="wait-dealt-father-box">
      <div class="wait-dealt-box" v-for="(item, i) in waitDealtList" :key="i" @click="toWaitDetails(item)">
        <h3>{{item.number}}</h3>
        <p>{{item.description}}</p>
        <p><span>上一个处理人：<b>{{item.prevOperator}}</b></span></p>
        <p><span>上一个处理时间：<b>{{item.prevOperatedTime}}</b></span></p>
      </div>
    </div>
    <van-empty
      v-show="waitDealtList.length == 0"
      class="custom-image"
      image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
      description="无待办事项"
    />
  </div>
</template>

<script>
import navBar from '@/components/navBar.vue'
export default {
  name: 'OverhaulManual',
  components: {
    navBar
  },
  data() {
    return {
      waitDealtList: [{}]
    }
  },
  methods: {
    toWaitDetails(val) { //  
      // 三合一确认页面不跳转待办详情页了（其实也可以跳转，但是那个页代码有点多，决定新开一个页面）
      if (this.$route.query.waitDealtName == '大检修规范化管理' || this.$route.query.waitDealtName == '检修统筹计划' || this.$route.query.waitDealtName == '检修专项排查') {
        this.$router.push({name: 'WaitDealtConfirm', query: {approvalId: val.id, waitDealtName: this.$route.query.waitDealtName, number: val.number}})
      } else {
        this.$router.push({name: 'WaitDealtDetails', query: {approvalId: val.id, waitDealtName: this.$route.query.waitDealtName, number: val.number, modulE: val.module}})
        console.log('waitDealtName', this.$route.query.waitDealtName)
      }
    },
    getList() {
      // debugger
      this.$getHttp(`${this.$baseUrl}/OS-PM/versions/${window.localStorage.getItem('versionId')}/backlogs`, 
      {
        params: {
          module: 
          this.$route.query.waitDealtName == 
          '方案管理' ? '检修方案' : this.$route.query.waitDealtName == 
          '大检修管理手册' ? '检修手册' : this.$route.query.waitDealtName
          // '大检修规范化管理' ? '' : this.$route.query.waitDealtName ==  //  接口问题 不传参先调通
          // '检修统筹计划' ? '' : this.$route.query.waitDealtName ==  //  接口问题 不传参先调通
          // '检修专项排查' ? '' : this.$route.query.waitDealtName //  接口问题 不传参先调通
          }
        }
      ).then(res=> {
        // debugger
        if (res.status == 200) {
          this.waitDealtList = res.data.results;
        }
      }).catch(err=> {
        // debugger
      })
    }
  },
  mounted() {
    this.getList()
  }
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .overhaul-manual {
    height: 100vh;
    overflow: hidden;
    box-sizing: border-box;
    padding: 0rem 1rem;
    padding-right: 0rem;
    .wait-dealt-father-box {
      height: calc(100% - 4.375rem);
      overflow: auto;
      box-sizing: border-box;
      padding-right: 1rem;
      .wait-dealt-box {
        width: 100%;
        // height: 7.625rem;
        border-radius: 0.75rem;
        background: #F3F3F3;
        box-sizing: border-box;
        padding: 0.8125rem 1rem;
        margin: 0.5rem 0rem;
        h3 {
          font-size: 0.875rem;
          font-weight: 400;
        }
        p {
          font-size: 0.75rem;
            color:#808187;
          margin: 0.25rem 0rem;
          span {
            color: #B4B4B4;
          }
        }
      }
    }
    .van-empty {
      height: calc(100vh - 5rem);
    }
  }
</style>